import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from 'react-router-dom';
import {
  AccountingPage,
  AddConductPage,
  BalanceTurnoverListPage,
  BankClientPage,
  ConductListPage,
  DocumentsPage,
  HomePage,
  InvoicesPage,
  MyOrganizationPage,
  MyOrganizationsPage,
  SignInPage,
  SignUpPage,
  AddInvoicePage,
  StaffAccountingPage,
  AddStuffPage,
  TransferStuffPage,
  FireStuffPage,
  TheoriesPage,
  TheoryTypePage,
  TheoryThemePage,
  TheoryTestingPage,
} from '../pages';
import { ROUTES } from '../constants/routes';

export const AppRouter = () => {
  return (
    <Router>
      <Switch>
        <Route path={ROUTES.SIGN_IN} exact={true} component={SignInPage} />
        <Route path={ROUTES.SIGN_UP} exact={true} component={SignUpPage} />
        <Route path={ROUTES.HOME} exact={true} component={HomePage} />
        <Route
          path={ROUTES.MY_ORGANIZATIONS}
          exact={true}
          component={MyOrganizationsPage}
        />
        <Route path={ROUTES.THEORIES} exact={true} component={TheoriesPage} />
        <Route
          path={ROUTES.THEORY_TYPE}
          exact={true}
          component={TheoryTypePage}
        />
        <Route
          path={ROUTES.THEORY_THEME}
          exact={true}
          component={TheoryThemePage}
        />
        <Route
          path={ROUTES.THEORY_TESTING}
          exact={true}
          component={TheoryTestingPage}
        />
        <Route
          path={ROUTES.PRACTICE}
          exact={true}
          component={MyOrganizationsPage}
        />
        <Route
          path={ROUTES.EXAM}
          exact={true}
          component={MyOrganizationsPage}
        />
        <Route
          path={ROUTES.MY_ORGANIZATION}
          exact={true}
          component={MyOrganizationPage}
        />
        <Route
          path={ROUTES.ACCOUNTING}
          exact={true}
          component={AccountingPage}
        />
        <Route
          path={ROUTES.BALANCE_TURNOVER_LIST}
          exact={true}
          component={BalanceTurnoverListPage}
        />
        <Route
          path={ROUTES.CONDUCT_LIST}
          exact={true}
          component={ConductListPage}
        />
        <Route
          path={ROUTES.ADD_CONDUCT}
          exact={true}
          component={AddConductPage}
        />
        <Route
          path={ROUTES.BANK_CLIENT}
          exact={true}
          component={BankClientPage}
        />
        <Route path={ROUTES.DOCUMENTS} exact={true} component={DocumentsPage} />
        <Route path={ROUTES.INVOICES} exact={true} component={InvoicesPage} />
        <Route
          path={ROUTES.ADD_INVOICE}
          exact={true}
          component={AddInvoicePage}
        />
        <Route
          path={ROUTES.STAFF_ACCOUNTING}
          exact={true}
          component={StaffAccountingPage}
        />
        <Route path={ROUTES.ADD_STAFF} exact={true} component={AddStuffPage} />
        <Route
          path={ROUTES.TRANSFER_STAFF}
          exact={true}
          component={TransferStuffPage}
        />
        <Route
          path={ROUTES.FIRE_STAFF}
          exact={true}
          component={FireStuffPage}
        />

        <Redirect exact from="/" to={ROUTES.SIGN_IN} />
      </Switch>
    </Router>
  );
};
