import React from 'react';
import { Form, Input, Button } from 'antd';
import styles from './styles.module.scss';
import { Link } from 'react-router-dom';
import { ROUTES } from '../../constants/routes';

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 8 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 16 },
  },
};
const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

const SignUpPageView = () => {
  const [form] = Form.useForm();

  const onFinish = (values: any) => {
    console.log('Received values of form: ', values);
  };

  return (
    <div className={styles.container}>
      <h1>Регистрация</h1>
      <Form
        {...formItemLayout}
        className={styles.form}
        form={form}
        name="register"
        onFinish={onFinish}
        initialValues={{}}
        scrollToFirstError>
        <Form.Item name="organizationName" label="Название организации">
          <Input size={'large'} />
        </Form.Item>
        <Form.Item name="directorFio" label="ФИО Директора">
          <Input size={'large'} />
        </Form.Item>
        <Form.Item name="activity_type" label="Вид деятельности">
          <Input size={'large'} />
        </Form.Item>
        <Form.Item name="phone" label="Номер телефона">
          <Input size={'large'} />
        </Form.Item>
        <Form.Item
          name="email"
          label="E-mail"
          rules={[
            {
              type: 'email',
              message: 'The input is not valid E-mail!',
            },
            {
              required: true,
              message: 'Please input your E-mail!',
            },
          ]}>
          <Input size={'large'} />
        </Form.Item>
        <Form.Item
          name="password"
          label="Пароль"
          rules={[
            {
              required: true,
              message: 'Please input your password!',
            },
          ]}
          hasFeedback>
          <Input.Password size={'large'} />
        </Form.Item>

        <Form.Item
          name="confirm"
          label="Подтвердите пароль"
          dependencies={['password']}
          hasFeedback
          rules={[
            {
              required: true,
              message: 'Please confirm your password!',
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('password') === value) {
                  return Promise.resolve();
                }
                return Promise.reject(
                  new Error('The two passwords that you entered do not match!'),
                );
              },
            }),
          ]}>
          <Input.Password size={'large'} />
        </Form.Item>
        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            size={'large'}
            className={styles.btn}>
            Зарегистрироваться
          </Button>
          <Link to={ROUTES.SIGN_IN} className={styles.center}>
            Авторизация
          </Link>
        </Form.Item>
      </Form>
    </div>
  );
};

export default SignUpPageView;
