export const route =
  (routeName: string, replacers: { [key: string]: any }) => () => {
    let routeNormalized = routeName;

    for (const prop in replacers) {
      routeNormalized = routeNormalized.replace(
        `:${prop}`,
        replacers[prop].toString(),
      );
    }

    return routeNormalized;
  };
