import React from 'react';
import styles from './styles.module.scss';
import { Header } from '../../components/Header';
import 'video-react/dist/video-react.css';
// @ts-ignore
import { Player } from 'video-react';
import { Upload, message, Button, Divider } from 'antd';
import { UploadOutlined, DownloadOutlined } from '@ant-design/icons';

const props = {
  name: 'file',
  action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
  headers: {
    authorization: 'authorization-text',
  },
  onChange(info: any) {
    if (info.file.status !== 'uploading') {
      console.log(info.file, info.fileList);
    }
    if (info.file.status === 'done') {
      message.success(`${info.file.name} file uploaded successfully`);
    } else if (info.file.status === 'error') {
      message.error(`${info.file.name} file upload failed.`);
    }
  },
};

const TheoryThemePageView = () => {
  return (
    <div className={styles.container}>
      <Header title={'Тема 1'} back />
      <div className={styles.menuItems}>
        <h1>
          Информация о темы <br /> Lorem ipsum dolor sit amet, consectetur
          adipisicing elit. Alias architecto dolores expedita facere incidunt
          molestias nam nesciunt recusandae veniam vero. Adipisci consectetur
          consequatur deleniti doloribus eveniet explicabo facilis harum omnis
          sed voluptates! A commodi cumque et, eveniet nam possimus praesentium
          quae quas rem temporibus. Ad hic itaque provident quas temporibus.
        </h1>
        <div className={styles.video}>
          <Player>
            <source src="https://media.w3.org/2010/05/sintel/trailer_hd.mp4" />
          </Player>
        </div>
        <div>
          <Button icon={<DownloadOutlined />}>
            Получит прикрепленное домашное задание
          </Button>
          <Divider />
          <Upload {...props}>
            <Button icon={<UploadOutlined />}>
              Прикрепление домашнего задание на проверку
            </Button>
          </Upload>
        </div>
      </div>
    </div>
  );
};

export default TheoryThemePageView;
